.footer {
  height: 200px;
  background-image: url(../images/footer.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;

  @include onTablet {
    height: 480px;
    background-image: url(../images/footerMiddle.png);
  }

  @include onDesktop {
    height: 560px;
    background-image: url(../images/footerBig.png);
  }
}
